<template>
    <v-dialog v-model="dialog" width="800">
        <v-card>
            <v-card-title class="text-h5">
                {{ create ? 'Ajouter' : 'Modifier' }} une <code class="ml-1">Application</code>
            </v-card-title>

            <v-card-text>
                <v-form v-model="valid">
                    <v-text-field v-model="modifiedApp.title" :counter="40" :rules="titleRules" label="Name" required></v-text-field>
                    <v-text-field v-model="modifiedApp.icon" label="Icon" required></v-text-field>
                    <v-text-field v-model="modifiedApp.link" label="Lien" required></v-text-field>
                    <v-select v-model="color" :items="['bleu', 'vert']" label="Couleur" menu-props="auto"></v-select>
                    <v-select v-model="modifiedApp.groups" :items="$groups" label="Groupes d'utilisateurs" required multiple smavaluell-chips></v-select>
                </v-form>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="grey" text @click="dialog = false">
                    Retour
                </v-btn>

                <v-btn color="green darken-1" text @click="create ? $emit('created', modifiedApp, color == 'bleu' ? '#b' : '#v') : $emit('updated', modifiedApp, color == 'bleu' ? '#b' : '#v'); dialog = false" :disabled="!valid">
                    {{ create ? 'Ajouter' : 'Modifier' }}
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>

export default {
    name: "appsModify",
    props: {
        open: Boolean,
        create: Boolean,
        app: Object
    },
    computed: {
        dialog: {
            get() {
                return this.open
            },
            set() {
                this.$emit('close')
            }
        },
        modifiedApp: {
            get() {
                if (this.updatedApp === undefined)
                    return this.app
                else
                    return this.updatedApp
            },
            set(value) {
                this.updatedApp = value
            }
        }
    },
    data: () => ({
        updatedApp: undefined,
        valid: false,
        color: 'bleu',
        titleRules: [
            v => !!v || 'Un titre est ncessaire',
            v => (v && v.length <= 100) || 'Un titre trop long risque de poser des problmes d\'affichage',
        ],
    }),
}
</script>